<template>
  <label class="radio">
    <input
      class="radio__input"
      type="radio"
      :id="id"
      :name="this.name"
      :checked="this.checked"
      :disabled="this.disabled"

      :value="value"
      @input="apdateRadio"
    />
    <span class="radio__btn"></span>
  </label>
</template>

<script>
export default {
  name: 'v-radio',
  props: {
    value: String,
    name: String,
    id: String,
    checked: String,
    disabled: String,
  },
  methods: {
    apdateRadio() {
      this.$emit('input', this.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.radio {
  padding-top: 4px;
  width: 20px;
  min-height: 20px;
  position: relative;
}
.radio__input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.radio__btn {
  position: absolute;
  top: 5px;
  left: 0;
  min-width: 20px;
  min-height: 20px;
  background-image: url("~@/assets/images/radio-default.svg");
  cursor: pointer;
  border-radius: 50%;
}
.radio__input:checked + .radio__btn {
  background-image: url("~@/assets/images/radio-cheсked.svg");
}
.radio__input:focus + .radio__btn {
  background-image: url("~@/assets/images/radio-default.svg");
  outline: solid 1px #1d85c9;
}
.radio__input:checked:focus + .radio__btn {
  background-image: url("~@/assets/images/radio-cheсked.svg");
}
.radio__input:disabled + .radio__btn {
  opacity: 0.5;
}
.radio__input:checked:disabled + .radio__btn {
  background-image: url("~@/assets/images/radio-cheсked.svg");
  opacity: 0.5;
}
</style>