<script>
import VRadio from "./components/v-radio.vue";
import VInput from "./components/v-input.vue";
import VCheck from "./components/v-check.vue";

export default {
  components: { VInput, VRadio, VCheck },
  data() {
    return {
      currentStep: 1,
      pageWidth: window.innerWidth,
      formReg: {
        //итоговый объект
        name: "",
        surname: "",
        patronymic: "",
        patronymicExist: true, //если отчество есть будет true, если нет, false
        sex: "",
        passport: {
          // паспорт
          snils: "",
          dateBorn: "",
          placeBorn: "",
          series: "",
          number: "",
          giveDate: "",
          codeDepartment: "",
          department: "",
        },
        inn: "", // может быть false
        telephoneNumber: "",
        homeNumber: "", // может быть false
        jobNumber: "", // может быть false
        Email: "",
        registerAddres: {
          region: "",
          district: "",
          town: "",
          locality: "",
          street: "",
          house: "",
          housing: "", // может быть false
          apartmentNumber: "", // может быть false
          index: "",
          residenceAddress: {
            // адрес проживания, если установлена галочка 'Адрес регистрации совпадает с адресом проживания', то residenceAddress = registerAddres, в ином случае поля теже
          },
          questions: {
            // true - да, flase - нет
            question1: false,
            question2: false,
            question3: false,
            question4: false,
            question5: false,
          },
          range: "1000 - 60000", // диапазон - это строка, типа '1000 - 60000'
        },
      },
    };
  },
  methods: {
    addStep() {
      this.currentStep += 1;
    },
    decStep() {
      this.currentStep -= 1;
    },
    disabledPatronymic(e) {
      this.formReg.patronymicExist = !e.checked;
      if (e.checked) {
        this.formReg.patronymic = "";
      }
    },
    setSex(e) {
      this.formReg.sex = e;
    },
  },
};
</script>

