<template>
  <label class="check">
    <input
      class="check__input"
      type="checkbox"
      :id="id"
      :name="this.name"
      :checked="this.checked"
      :disabled="this.disabled"

      @click="apdatecheckBox"
    />
    <span class="check__box"></span>
  </label>
</template>

<script>
export default {
  name: 'v-checkbox',
  props: {
    name: String,
    id: String,
    checked: String,
    disabled: String,
  },
  methods: {
    apdatecheckBox(e) {
      this.$emit('input', e.target);
    }
  }
};
</script>

<style lang="scss" scoped>
.check {
  padding-top: 4px;
  width: 20px;
  min-height: 20px;
  position: relative;
}
.check__input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.check__box {
  position: absolute;
  top: 5px;
  left: 0;
  min-width: 20px;
  min-height: 20px;
  background-image: url("~@/assets/images/check-default.svg");
  cursor: pointer;
  border-radius: 4px;
}
.check__input:checked + .check__box {
  background-image: url("~@/assets/images/check-checked.svg");
}
.check__input:focus + .check__box {
  background-image: url("~@/assets/images/check-default.svg");
  outline: solid 4px rgba(29, 132, 201, 0.4);
}
.check__input:checked:focus + .check__box {
  background-image: url("~@/assets/images/check-checked.svg");
}
.check__input:disabled + .check__box {
  opacity: 0.5;
}
.check__input:checked:disabled + .check__box {
  background-image: url("~@/assets/images/check-checked.svg");
  opacity: 0.5;
}
</style>